import { trim, uniqBy } from "lodash";
import { parseDate } from "./parse-date";
import { SoldShare } from "./types";

export const parseSoldShares = (text: string): SoldShare[] => {
  let shares: SoldShare[] = [];
  const lines = trim(text)
    .replace(/Sell of Restricted Stock /g, "")
    .replace(/Sell of Stock /g, "")
    .replace(/Same Day Sell /g, "")
    .split(/[\n\r]+/);

  // Skip empty file
  if (lines[0] === "") {
    return shares;
  }

  lines.forEach((line) => {
    const [orderNumber, type, grantNumber, grantDate, grantType, orderDate, sharesSold, salePrice, exercisePrice, totalFees] = line.split(" ");
    shares.push({
      orderNumber,
      grantNumber,
      grantDate: parseDate(grantDate),
      grantType,
      orderDate: parseDate(orderDate),
      sharesSold: Number(sharesSold),
      salePrice: Number(salePrice.replace("$", "")),
      exercisePrice: Number(exercisePrice.replace("$", "")),
      totalFees: Number(totalFees.replace("$", "")),
    });
  });

  return uniqBy(shares, ({ orderNumber }) => orderNumber);
}; 