import { trim, uniqBy } from "lodash";
import { parseDate } from "./parse-date";
import { IssuedShare } from "./types";

export const parseIssuedShares = (text: string): IssuedShare[] => {
  let shares: IssuedShare[] = [];
  const lines = trim(text).split(/[\n\r]+/).filter(line => line);

  lines.forEach(line => {
    const [grantDate, grantNumber, grantType, vestingDate, vestedShares, stockPrice, exercisePrice] = line.split(" ");
    shares.push({
      grantDate: parseDate(grantDate),
      grantNumber,
      grantType,
      vestingDate: parseDate(vestingDate),
      vestedShares: Number(vestedShares),
      stockPrice: Number(stockPrice.replace("$", "")),
      exercisePrice: Number(exercisePrice.replace("$", "")),
    });
  });

  return uniqBy(shares, ({ grantNumber }) => grantNumber);
};
