import { trim, uniqBy } from "lodash";
import { parseDate } from "./parse-date";
import { IssuedShare } from "./types";

export const parseSameDayShares = (text: string): IssuedShare[] => {
  let shares: IssuedShare[] = [];
  const lines = trim(text)
    .split(/[\n\r]+/)
    .filter((line) => line.includes("Same Day Sell"))
    .map((line) => line.replace(/Same Day Sell /g, ""));

  if (lines[0] === "") {
    return shares;
  }

  lines.forEach((line) => {
    const [orderNumber, grantNumber, grantDate, grantType, orderDate, sharesSold, salePrice, exercisePrice] = line.split(" ");
    shares.push({
      grantDate: parseDate(grantDate),
      grantNumber,
      grantType,
      vestingDate: parseDate(orderDate),
      vestedShares: Number(sharesSold),
      stockPrice: Number(salePrice.replace("$", "")),
      exercisePrice: Number(exercisePrice.replace("$", "")),
    });
  });

  return uniqBy(shares, ({ orderNumber }) => orderNumber);
}; 